.flex {
  display: flex;
  align-items: center;
}

.noStyle {
  list-style: none;
  margin: 0;
  padding: 0;
}

.navLink,
.darkNavLink {
  padding: 10px 15px;
  display: block;
  background-color: darkgray;
  border-radius: 4px;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.button:hover {
  filter: brightness(1.1);
}

.navLink,
.darkNavLink {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.navArea {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.darkNavLink {
  background-color: white;
  color: black;
}
