.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  overflow-x: hidden;
}

.fotoArea {
  display: grid;
  gap: 20px;
}

.infoArea {
  margin-left: 20px;
}

h2 {
  margin: 10px 0;
}

.price {
  display: inline-block;
  padding: 8px 15px;
  background-color: #91fb5d;
  border-radius: 5px;
  color: #017e01;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 10px;
  font-weight: bold;
}

body.dark .Product_price__v4vr3 {
  background-color: red;
}
.desc {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2rem;
  line-height: 1.2;
}
