.ulProdutos {
  list-style: none;
  margin: 0;
  padding: 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

@media only screen and (max-width: 767px) {
  .grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (min-width: 0px) and (max-width: 380px) {
  .grid {
    grid-template-columns: 1fr;
  }

  .ulProdutos li {
    display: grid;
    place-items: center;
    align-items: center;
  }
}
